function useTranslate() {
  const [translated, setTransalted] = useState(false);
  useEffect(() => {
    const listenKey = "immersiveTranslateDocumentMessageTellThirdParty";
    const listener = (e: any) => {
      const { detail } = e;
      if (detail) {
        try {
          const detailObj = JSON.parse(detail);
          if (detailObj.type === "paragraphTranslated") {
            const { ok } = detailObj.payload;
            setTransalted(ok);
          } else {
          }
        } catch (e) {
          console.error(e);
        }
      }
    };
    document.addEventListener(listenKey, listener);
    return () => {
      document.removeEventListener(listenKey, listener);
    };
  }, []);
  return translated;
}
